
import { useState, useEffect, useCallback } from "react";
import { isAuth, getId } from '../jwtCheck';
import axios from 'axios';


import { useNavigate } from "react-router-dom";
import {
    Box,
    Container,
} from '@mui/material/';
import './User.css';

import Swal from 'sweetalert2';

const User = (props) => {

    const token = JSON.parse(localStorage.getItem('accessToken'));
    const userId = getId(token);
    const navigate = useNavigate();



    const [user, setUser] = useState([]);
    const [nic, setNick] = useState();
    const [state, setsState] = useState();
    const [oldPas, setOldPas] = useState();
    const [newPas, setNewPas] = useState();
    const [passwordConfirm, setPasswordConfirm] = useState('');

    // 
    const [nameMessage, setNameMessage] = useState('');
    const [passwordOldMessage, setPasswordOldMessage] = useState('');
    const [passwordNewMessage, setPasswordNewMessage] = useState('');
    const [passwordConfirmMessage, setPasswordConfirmMessage] = useState('');

    //  
    const [isName, setIsName] = useState(true);
    const [isOldPassword, setIsOldPassword] = useState(false);
    const [isNewPassword, setIsNewPassword] = useState(false);
    const [isPasswordConfirm, setIsPasswordConfirm] = useState(false);


    function getUser() {
        axios.get('/api/users/' + userId, { params: { id: userId } })
            .then(function (json) {
                setUser(json.data);
                setNick(json.data.nickname);
            });


    };
    useEffect(() => {
        getUser();
        if (!isAuth(token)) {
            Swal.fire({
                confirmButtonColor: '#ffa500',
                confirmButtonText: '',
                text: '    ', // Alert 

            });
            navigate('/login');
        }
    }, []);
    let nicBody = {
        id: userId,
        nickname: nic

    };


    const handleSubmitNic = (e) => {
        e.preventDefault();

        const nameRegex = /^[-|a-zA-Z|0-9]+$/;

        if (!nameRegex.test(nic) || nic.length < 1) {
            setNameMessage('  !');
            setIsName(false);
        } else {
            setIsName(true);
            axios

                .post('/api/user/' + userId + '/nickname', nicBody)

                .then(function (response) {
                    if (response.data == false) {
                        Swal.fire({
                            confirmButtonColor: '#ffa500',

                            confirmButtonText: '',
                            text: ' !', // Alert 

                        });

                    } else {
                        Swal.fire({
                            confirmButtonColor: '#ffa500',

                            confirmButtonText: '',
                            html: ' .<br> !', // Alert 

                        }).then((re) => {
                            if (re.isConfirmed) {
                                localStorage.clear();
                                props.setUserNickName('');
                                navigate('/');
                            }
                        });

                    }



                })
                .catch(function (err) {
                    console.log(err);
                });
        }
    };

    let pasBody = {
        id: userId,
        oldPassword: oldPas,
        newPassword: newPas

    };
    const handleSubmitPas = useCallback((e) => {
        e.preventDefault();

        const passwordRegex = /^.{4,20}$/;
        if (!passwordRegex.test(oldPas)) {
            setPasswordOldMessage('4~20 !');
            setIsOldPassword(false);

        } else if (!passwordRegex.test(newPas)) {
            setPasswordNewMessage('4~20 !');
            setIsOldPassword(true);
            setIsNewPassword(false);
        } else if (passwordConfirm != newPas) {
            setIsPasswordConfirm(false);
            setPasswordConfirmMessage(' !');
            setIsOldPassword(true);
            setIsNewPassword(true);
        } else {
            setIsOldPassword(true);
            setIsNewPassword(true);
            setIsPasswordConfirm(true);
            axios
                .post('/api/user/' + userId + '/password', pasBody)
                .then(function (response) {
                    if (response.data == false) {
                        Swal.fire({
                            confirmButtonColor: '#ffa500',

                            confirmButtonText: '',
                            text: ' !', // Alert 

                        });

                    } else {
                        Swal.fire({
                            confirmButtonColor: '#ffa500',

                            confirmButtonText: '',
                            html: ' .<br> !', // Alert 

                        }).then((re) => {
                            if (re.isConfirmed) {
                                localStorage.clear();
                                props.setUserNickName('');
                                navigate('/');
                            }
                        });

                    }


                })
                .catch(function (err) {
                    console.log(err);
                });

        }



    }, [oldPas, newPas, passwordConfirm]
    );

    let delBody = {
        id: userId,


    };
    const handleSubmitDel = (e) => {
        e.preventDefault();

        Swal.fire({
            showCancelButton: true,
            confirmButtonColor: '#2fbe9f',
            cancelButtonColor: '#fd565f',
            confirmButtonText: '',
            cancelButtonText: '',
            text: ' ?', // Alert 
        }).then((result) => {
            if (result.isConfirmed) {
                axios
                    .post('/api/user/delete', delBody)
                    .then(function (response) {
                        console.log(response.status, '');
                        Swal.fire({

                            confirmButtonColor: '#ffa500',

                            confirmButtonText: '',

                            text: '!', // Alert 
                        }).then((re) => {
                            if (re.isConfirmed) {
                                localStorage.clear();
                                props.setUserNickName('');

                                navigate('/');
                            }
                        });
                    })
                    .catch(function (err) {
                        console.log(delBody);
                        console.log(err);

                    });
            }

        });
    };
    // 
    const onChangeName = useCallback((e) => {
        const nameRegex = /^[-|a-zA-Z|0-9]+$/;
        const nameCurrent = e.target.value;
        setNick(nameCurrent);

        if (!nameRegex.test(nameCurrent) || nameCurrent.length < 1) {
            setNameMessage('  !');
            setIsName(false);
        } else {

            setIsName(true);
        }
    }, []);

    //  
    const onChangeOldPassword = useCallback((e) => {
        const passwordRegex = /^.{4,20}$/;
        const passwordOldCurrent = e.target.value;
        setOldPas(passwordOldCurrent);

        if (!passwordRegex.test(passwordOldCurrent)) {
            setPasswordOldMessage('4~20 !');
            setIsOldPassword(false);
        } else {

            setIsOldPassword(true);
        }
    }, []);
    //  
    const onChangeNewPassword = useCallback((e) => {
        const passwordRegex = /^.{4,20}$/;
        const passwordNewCurrent = e.target.value;
        setNewPas(passwordNewCurrent);

        if (!passwordRegex.test(passwordNewCurrent)) {
            setPasswordNewMessage('4~20 !');
            setIsNewPassword(false);
        } else {

            setIsNewPassword(true);
        }
    }, []);
    // 
    const onChangePasswordConfirm = useCallback(
        (e) => {
            const passwordConfirmCurrent = e.target.value;
            setPasswordConfirm(passwordConfirmCurrent);

            if (newPas === passwordConfirmCurrent) {

                setIsPasswordConfirm(true);
            } else {
                setPasswordConfirmMessage(' !');
                setIsPasswordConfirm(false);
            }
        },
        [newPas]
    );
    return (<div style={{ height: '100vh', paddingTop: '130px' }}>
        <h3 style={{
            textAlign: 'center', color: '#58CCFF', fontWeight: '600',
            textShadow: '-1px -1px 0 #000, 1px -1px 0 #000,-1px 1px 0 #000,1px 1px 0 #000'
        }} > </h3>
        <br />
        <Container className="UserEditor"
            sx={{
                height: 560,
                width: 400,
                display: 'flex',
                flexDirection: 'column',
                borderRadius: '10px',
                padding: '10px 50px',
                backgroundColor: '#fff',
                boxShadow: ' 0 8px 20px 0 rgba(0, 0, 0, 0.15)'
            }}>


            <br></br>
            <Box component="form" sx={{ mt: 3 }}>
                <div className="userFlex">
                    <label></label>
                    <input
                        defaultValue={user.email}
                        name="nickName"
                        type="text"
                        readOnly

                    />

                </div>
                <div className="userFlex">
                    <label></label>
                    <input
                        defaultValue={nic}
                        name="nickName"
                        onChange={onChangeName}
                        type="text"
                    />
                    {<span className={`message ${isName ? 'success' : 'error'}`}>{nameMessage}</span>}
                    <div className='modify'>
                        <button onClick={handleSubmitNic}></button>
                    </div>

                </div>
                <br></br>
                <div className="userFlex">
                    <label> </label>
                    <input
                        defaultValue={oldPas}
                        name="old"
                        onChange={onChangeOldPassword}
                        type="password"
                    />
                    {(
                        <span className={`message ${isOldPassword ? 'success' : 'error'}`}>{passwordOldMessage}</span>
                    )}
                </div>

                <div className="userFlex">
                    <label> </label>
                    <input
                        defaultValue={newPas}
                        name="new"
                        onChange={onChangeNewPassword}
                        type="password"
                    />
                    {(
                        <span className={`message ${isNewPassword ? 'success' : 'error'}`}>{passwordNewMessage}</span>
                    )}
                </div>

                <div className="userFlex">
                    <label>  </label>
                    <input
                        defaultValue={passwordConfirm}
                        onChange={onChangePasswordConfirm}
                        name="confirm"
                        type="password"


                    />
                    {(
                        <span className={`message ${isPasswordConfirm ? 'success' : 'error'}`}>{passwordConfirmMessage}</span>
                    )}
                    <div className='modify2'>
                        <button onClick={handleSubmitPas}></button>
                    </div>
                </div>

            </Box>



            <div className="userDelBtn">
                <button onClick={handleSubmitDel}></button>
            </div>

        </Container>
    </div>
    );

};
export default User;